import { TooltipField } from '@/features/projects/2-components/innovation-form/TooltipField'
import { SUPPORT_PROVIDERS } from '@/features/projects/2-components/innovation-form/types'
import { Checkbox } from '@mantine/core'
import { Label } from '@radix-ui/react-dropdown-menu'
import React from 'react'

// Define FormData type with pastFunding property
type PastFunding = {
  hasPastFunding: boolean;
  funders?: string[];
  // add other fields as needed
};

type FormData = {
  pastFunding: PastFunding;
  // add other fields as needed
};

const SelectFunder = () => {
  const [formData, setFormData] = React.useState<FormData>({
    pastFunding: {
      hasPastFunding: false,
      funders: [],
    },
  });

  const handlePastFundingUpdate = <K extends keyof PastFunding>(
    field: K,
    value: PastFunding[K]
  ) => {
    setFormData((prev) => ({
      ...prev,
      pastFunding: {
        ...prev.pastFunding,
        [field]: value,
      },
    }));
  };

  const updateFormData = (data: { pastFunding: PastFunding }) => {
    setFormData((prev) => ({
      ...prev,
      pastFunding: {
        ...prev.pastFunding,
        ...data.pastFunding,
      },
    }));
  };

  const handleFunderChange = (provider: string, checked: boolean) => {
    setFormData((prev) => {
      const funders = prev.pastFunding.funders || [];
      return {
        ...prev,
        pastFunding: {
          ...prev.pastFunding,
          funders: checked
            ? [...funders, provider]
            : funders.filter((f) => f !== provider),
        },
      };
    });
  };

  return (
    <div>

      <TooltipField
        label="Do you want to select the name of the funder to send the report to?"
        tooltip="Select whether you want to select the certain funder to send the report to."
        id="past-funding"
      >
        <div className="space-y-3">
          <div className="flex items-center space-x-2">
            <Checkbox
              id="has-past-funding"
              checked={formData.pastFunding.hasPastFunding}
              onChange={(event) =>
                updateFormData({
                  pastFunding: {
                    ...formData.pastFunding,
                    hasPastFunding: event.target.checked,
                  },
                })
              }
            />
            <Label>Yes, we have received past funding or support</Label>
          </div>
        </div>
      </TooltipField>

      {formData.pastFunding?.hasPastFunding && (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <div>
            <TooltipField
              label="Previous Funders"
              tooltip="Select all organizations that have provided funding"
              id="previous-funders"
            >
              <div className="space-y-2 max-h-48 overflow-y-auto border rounded-md p-3">
                {SUPPORT_PROVIDERS.map((provider) => (
                  <div key={provider} className="flex items-center space-x-2">
                    <Checkbox
                      id={`funder-${provider}`}
                      checked={formData.pastFunding?.funders?.includes(provider) || false}
                      onChange={(event) =>
                        handleFunderChange(provider, event.target.checked)
                      }
                    />
                    <Label className="text-sm">
                      {provider}
                    </Label>
                  </div>
                ))}
              </div>
            </TooltipField>
          </div>
        </div>
      )}
    </div>
  );
}

export default SelectFunder
function updateFormData(arg0: { pastFunding: any }) {
    throw new Error('Function not implemented.')
}

